import module from "../../../core/systemServices/module_platformBoot";
import TEMPLATE from "./AppLoading-template.html";

module.component('msxAppLoading', {
	bindings: {},
	template: TEMPLATE,
	controller: ["msx.loggerService", "$state", "$localStorage", "$timeout", "$window", "$location", "$element",
		function(loggerService, $state, $localStorage, $timeout, $window, $location, $element){
		const angular = window["angular"];
		const $ = angular.element;
		$element.addClass("app-loading");
		$("body").removeClass("slide-menu-state-expanded");
		var loadLimit = 1000 * (60 * 3); // Five minutes is our limit for sitting omn loading before the UI tries to recover.
		$timeout(function(){
			var currentState = $state.current;
			var reloadCount = $localStorage.__reloadCount || 0;
			if(currentState.name === "auth.appLoading"){
				if(reloadCount < 5){
					reloadCount++;
					loggerService.warn("We have been stuck in loading a long time; there may be network issues causing problems with load.  Triggering a reload/transition force.  Current Reload attempt: " + reloadCount);
					$localStorage.__reloadCount = reloadCount;
					const deeplinkUrl = $localStorage.loadingScreenDeeplinkUrl || null;
					$location.url(deeplinkUrl);
					$window.location.reload();
				}else{
					loggerService.error("Unable to log in.  Something is wrong in the back end causing the UI to be unable to get past loading required information for the initialization.");
				}
			}else{
				$localStorage.__reloadCount = null;
				$localStorage.loadingScreenDeeplinkUrl = null;
			}
		}, loadLimit);
	}]
});

