import TEMPLATE from "./InputBox-template.html";
import module from "../index.js";

// This is an internal cached variable used to assign an fieldId if it is not already assigned.
var inputFieldCounter = 0;

var defaultPlaceholders = {
	cidr: "127.0.0.1/24",
	cidrip: "127.0.0.1/24",
	cidripv4: "127.0.0.1/24",
	cidripv6: "2001:DB8:C003:1::F00D/64",
	email: "cisco.common.placeholders.email",
	fqdn: "cisco.common.placeholders.fqdn",
	ip: "127.0.0.1",
	ipaddr: "127.0.0.1",
	ipaddress: "127.0.0.1",
	ipv4: "127.0.0.1",
	ipv6: "2001:DB8:C003:1::F00D",
	latitude: "45.3413",
	longitude: "-75.9278",
	phone: "+0 123 456-7890",
	phonenumber: "+0 123 456-7890",
	subnet: "255.255.255.0",
	netmask: "255.255.255.0",
	url: "cisco.common.placeholders.url"
};

module.component("msxInputBox", {
	template: TEMPLATE,
	bindings: {
		type: "@?",
		inputType: "@?",
		fieldId: "@?",
		name: "@?",
		autofocus: "@?",
		autocomplete: "@?",
		placeholder: "@?",
		msxModel: "=?",
		msxClass: "@?",
		maxlength: "@?",
		minlength: "@?",
		max: "@?",
		min: "@?",
		msxPattern: "@?",
		formname: "@?",
		label: "@?",
		inputErrorMessage: "@?",
		msxRequired: "<?",
		msxDisabled: "<?",
		msxReadonly: "<?",
		msxFocus: "&?",
		msxBlur: "&?",
		msxChange: "&?",
		msxTooltip: "@?",
		msxInvalid: "<?",
		msxTrim: "<?",
		showLabel: "<?",
		msxTabindex: "@?",
		msxForceShowErrorMessage:"<?",
		allowInvalid: "@",
		allowPasswordReveal: "<?",
		passwordRetrievalFunc: "=?",
		showCopy: "<?"
	},
	controller: ["$timeout", "_", "$rootScope", "$scope", "$localStorage", "$element", "msx.i18nMixins", "msx.a11yMixins", "msx.loggerService",
		function($timeout, _, $rootScope, $scope, $localStorage, $element, i18nMixins, a11yMixins, loggerService) {
		const self = this;
		const $ = window["jQuery"];
		$element.addClass("msx-input-box");

		i18nMixins.mixinTo(self);
		a11yMixins.mixinTo(self);
		const translate = $rootScope.resolveResourceString;

		const getModelController = function(){
			if(!self.inputData){
				var elem = $("input", $element)[0];
				if(elem){
					var key, obj;
					for(key in elem){
						obj = elem[key];
						if(typeof obj === "object"){
							if(obj != null && obj.$ngModelController){
								self.inputData = obj.$ngModelController;
								if(self.msxInvalid){
									self.inputData.$setValidity("", false);
									self.inputData.$setDirty("", true);
								}
							}
						}
					}
				}
			}
			return self.inputData;
		};

		const checkAndSetFieldState = function(changes){
			const isTrue = function(val){
				if(val != null && typeof val === "string"){
					if(val.toLowerCase() === "true"){
						return true;
					}
				}
				return false;
			};

			// Allow to set trim to false so onChange gets called when whitespace character is entered
			self._msxTrim = self.msxTrim !== undefined ? self.msxTrim : true;

			const msxDisabled = changes["msxDisabled"] ? changes["msxDisabled"] : {currentValue: self.msxDisabled};
			const msxReadonly = changes["msxReadonly"] ? changes["msxReadonly"] : {currentValue: self.msxReadonly};
			const msxRequired = changes["msxRequired"] ? changes["msxRequired"] : {currentValue: self.msxRequired};
			const msxDF = function(){
				return msxDisabled ? Boolean(msxDisabled.currentValue) : false;
			};
			const msxROF = function(){
				return msxReadonly ? Boolean(msxReadonly.currentValue) : false;
			};
			const msxReqF = function(){
				return msxRequired ? Boolean(msxRequired.currentValue) : false;
			};
			self._msxDisabled = msxDF();
			self._msxReadonly = msxROF();
			self._msxRequired = msxReqF();

			if (isTrue(msxDF()) || isTrue(msxROF())) {
				$element.blur();
				self.msxTabindex = -1;
				if(isTrue(msxDF())){
					$($element).addClass("vms-input-disabled");
				}
				if(isTrue(msxROF())){
					$($element).addClass("vms-input-readonly");
				}
			} else {
				self.msxTabindex = 0;
				$($element).removeClass("vms-input-readonly");
				$($element).removeClass("vms-input-disabled");
			}
		};

		self.showStarWithoutLabel = function() {
			return Boolean(self.msxRequired) && !self._showLabel;
		};

		self.showStarWithLabel = function(){
			return Boolean(self.msxRequired) && self._showLabel;
		};

		var errTypes = {
			"cidr": "cidr_ip",
			"cidrip": "cidr_ip",
			"cidripv4": "cidr_ipv4",
			"cidripv6": "cidr_ipv6",
			"email": "email",
			"fqdn": "fqdn",
			"integer": "integer",
			"ip": "ip_address",
			"ipaddr": "ip_address",
			"ipaddress": "ip_address",
			"ipv4": "ipv4",
			"ipv6": "ipv6",
			"latitude": "latitude",
			"longitude": "longitude",
			"phone": "phone_number",
			"phonenumber": "phone_number",
			"encsserial": "encs_serial",
			"sitename": "site_name",
			"subnet": "subnet",
			"netmask": "subnet",
			"url": "url"
		};

		self.resolveError = function(errObj){
			let message = "";
			_.forIn(errTypes, function(value, key){
				if(errObj[key]){
					if(message.length){
						message += " ";
					}
					message += translate("cisco.common.validation." + value);
				}
			});
			return message;
		};

		self.$onChanges = function(changes){

			// Handle states.
			checkAndSetFieldState(changes);

			// Handle other properties.
			const msxTooltip = changes["msxTooltip"] ? changes["msxTooltip"].currentValue || "" : self.msxTooltip;
			$("[data-toggle='tooltip']", $element).each(function() {
				const title = translate(msxTooltip);
				$(this).attr("data-original-title", title || "");
				$(this).attr("title", "");
			});

			const autocomplete = changes["autocomplete"] ? changes["autocomplete"].currentValue || "" : self.autocomplete;
			$element.find("input").attr("autocomplete", autocomplete);

			const msxPattern = changes["msxPattern"] ? changes["msxPattern"].currentValue || "" : self.msxPattern;
			if (typeof msxPattern === "string" && msxPattern !== "") {
				if (msxPattern.charAt(0) === "/" && msxPattern.charAt(msxPattern.length - 1) === "/") {
					self.__msxPattern = new RegExp(msxPattern.substring(1, msxPattern.length - 1));
				} else if (msxPattern === "'\\\\d+'") {
					// Work around odd escape issue.
					self.__msxPattern = new RegExp("^\\d+$");
				} else {
					self.__msxPattern = new RegExp(msxPattern);
				}
			} else {
				self.__msxPattern = null;
			}

			// Allow to force the error state if msxInvalid is not undefined
			if (changes['msxInvalid'] && changes['msxInvalid'].currentValue !== undefined) {
				if (self.inputData) {
					const isValid = !changes['msxInvalid'].currentValue;
					self.inputData.$setValidity("", isValid);
					if(!isValid){
						self.inputData.$setDirty("", true);
					}
				}
			}

			self._msxTrim = (changes['msxTrim'] || {}).currentValue !== undefined ? changes['msxTrim'].currentValue : self._msxTrim;

			const placeholder = changes["placeholder"] ? changes["placeholder"].currentValue ||
				(defaultPlaceholders[self.type] && translate(defaultPlaceholders[self.type])) ||
				// If no changes default to the placeholder we've already resolved
				"" : self._placeholder;

			self._placeholder = placeholder ? translate(placeholder) : "";

			const showLabel = changes["showLabel"] ? changes["showLabel"].currentValue : self.showLabel;
			if (showLabel == null) {
				// By default, we show the label.
				self._showLabel = true;
			} else {
				// Users can turn off showlabel if they want by
				// passing "false"
				if (typeof showLabel === "string") {
					const s = self._showLabel.toLowerCase();
					if (s === "false") {
						self._showLabel = false;
					} else {
						self._showLabel = true;
					}
				} else if (typeof showLabel == "boolean") {
					self._showLabel = showLabel;
				}
			}
		};

		self._valueChanged = function(value){
			self.copyBtnTooltip = 'cisco.common.inputbox.copy.button.tooltip.copy';
			if(self.msxChange){
				const mController = getModelController();
				self.msxChange({"$value": value, "inputDataStatus": {"$valid": mController ? mController.$valid : true, "$pristine": mController ? mController.$pristine : true, "$dirty": mController ? mController.$dirty : false}});
			}
		};

		self._getFieldType = function(){
			return self._forcedType || self.inputType || self.type;
		};

		self._showPassword = function(){
			if(self.type === "password" && !self._gettingPassword){
				const showPassword = function(password){
					self.msxModel = password;
					self._forcedType = "text";
				};
				if(self.passwordRetrievalFunc && typeof self.passwordRetrievalFunc === "function"){
					self._loadingPassword = true;
					const res = self.passwordRetrievalFunc();
					if(typeof res === "string"){
						self._loadingPassword = false;
						showPassword(res);
					}else if(res && res.then){
						res.then(function(password){
							showPassword(password);
						})["finally"](function(){
							self._loadingPassword = false;
						});
					}else{
						self._loadingPassword = false;
					}
				}else{
					showPassword(self.msxModel);
				}
			}
		};

		self._hidePassword = function(){
			self._forcedType = null;
		};

		self._copyInput = function(text){
			let result = "";
			try {
				let $tmpCopyElement = $('<textarea />');
				$tmpCopyElement.val(text).css({ top: "-10000px", position: "absolute", height: "100px", width: "100px" }).appendTo('body');
				$tmpCopyElement.select();
				result = document.execCommand('copy');
				$tmpCopyElement.remove();
			} catch(err){
				loggerService.log("### Unable to copy Input value: " + err);
				result = "";
			}
			self.copyBtnTooltip = 'cisco.common.inputbox.copy.button.tooltip.copied';
			return result;
		};

		if (!self.msxPattern) {
			$element.find("input").removeAttr("ng-pattern");
			self.__msxPattern = null;
		} else {
			if (typeof self.msxPattern === "string" && self.msxPattern !== "") {
				if (self.msxPattern.charAt(0) === "/" && self.msxPattern.charAt(self.msxPattern.length - 1) === "/") {
					self.__msxPattern = new RegExp(self.msxPattern.substring(1, self.msxPattern.length - 1));
				} else if (self.msxPattern === "'\\\\d+'") {
					// Work around odd escape issue.
					self.__msxPattern = new RegExp("^\\d+$");
				} else {
					self.__msxPattern = new RegExp(self.msxPattern);
				}
			} else {
				self.__msxPattern = self.msxPattern;
			}
		}

		self.$postLink = function(){
			// Establish the toolkit link on the icon, and watch the value
			// so that the original title can be updated when the property is.

			self._placeholder = self.placeholder ? translate(self.placeholder) : (defaultPlaceholders[self.type] && translate(defaultPlaceholders[self.type]));
			self.copyBtnTooltip = 'cisco.common.inputbox.copy.button.tooltip.copy';

			if (self.msxTooltip) {
				if (!self.msxTooltipLocation) {
					self.msxTooltipLocation = "right";
				}
			}

			var ngModelOptions = {updateOn: 'default blur click', debounce: { 'default': 500, 'blur': 0 }};
			if(self.allowInvalid === "true"){
				ngModelOptions["allowInvalid"] = true;
			}
			self.ngModelOptions = ngModelOptions;

			if (self.showLabel == null) {
				// By default, we show the label.
				self._showLabel = true;
			} else {
				// Users can turn off showlabel if they want by
				// passing "false"
				if (typeof self.showLabel === "string") {
					const s = self._showLabel.toLowerCase();
					if (s === "false") {
						self._showLabel = false;
					} else {
						self._showLabel = true;
					}
				} else if (typeof self.showLabel == "boolean") {
					self._showLabel = self.showLabel;
				}
			}

			if (self.ariaInputlabel) {
				$timeout(function() {
					// execuring async, otherwise the change doesn"t always apply.
					self.ariaInputlabel = translate(self.ariaInputlabel) || self.ariaInputlabel;
				}, 0);
			}

			if (!self.ariaInputlabel && self.label) {
				self.ariaInputlabel = translate(self.label);
			}

			if (!self.fieldId) {
				// If we don"t have an element id defined, compute a unique one.  This is so the label and the
				// input field can properly map for accessibiliry with for=inputFieldId on the label.
				// This should always be unique as it has an incrementor, and it is basing off time, the two
				// combined should always be unique.
				self.fieldId = "msx-input-box-" + inputFieldCounter++ + "-" + new Date().getTime();
			}

			$("[data-toggle='tooltip']", $element).each(function() {
				const title = translate(self.msxTooltip);
				$(this).attr("title", title || "");
			});
			$("[data-toggle='tooltip']", $element).tooltip({
				container: "body"
			});
			$("[data-toggle='tooltip']", $element).each(function() {
				const title = translate(self.msxTooltip);
				$(this).attr("title", "");
			});

			$element.find("input").attr("autocomplete", self.autocomplete);
			$scope.$on("updated-active-language", function() {
				var update = (self.placeholder && translate(self.placeholder)) || "";
				self._placeholder = update;
			});
			let count = 0;
			const findController = function(){
				count++;
				if(getModelController() == null){
					$timeout(findController, 10 + (count*10));
				}
			};
			$timeout(findController, 10);
		};
	}]
});