import module from "../../../core/systemServices/module_platformBoot";
import TEMPLATE from "./PasswordExpiryDialog-template.html";

module.component("msxPasswordExpiryDialog", {
	template: TEMPLATE,
	bindings: {
		modalInstance: "<?",
		resolve: "<?"
	},
	controller: [function(){
		const self = this;
		self.ok = function(){
			if(typeof self.resolve.handler === "function"){
				self.resolve.handler();
				self.modalInstance.close();
			}		
		};
		self.$onChanges = function(changes){
			const resolve = changes["resolve"];
			if(resolve && resolve.currentValue && resolve.currentValue.message){
				self.expirationMessage = resolve.currentValue.message;
			}
		};
	}]
});