import bootModule from "./module_platformBoot";
const angular = window["angular"];

angular.module("skPlatform", ["ngStorage"]);
angular.module("skPlatformBootPrereqs", ["ngStorage"]);	
bootModule
	.constant("_", window._)
	.constant('DOMPurify', window.DOMPurify)
	.constant('he', window.he)
	.config(["$httpProvider", "$stateProvider", "$locationProvider", "$urlRouterProvider", "routerProvider",
		"$controllerProvider", "$compileProvider", "$provide", "$localStorageProvider",
		function($httpProvider, $stateProvider, $locationProvider, $urlRouterProvider, router,
			$controllerProvider, $compileProvider, $provide, $localStorageProvider) {
			try {
				if($locationProvider && typeof $locationProvider.hashPrefix === "function"){
					$locationProvider.hashPrefix("");
				}
				
				//To disable IE cache GET calls globally
				if (!$httpProvider.defaults.headers.get) {
					$httpProvider.defaults.headers.get = {};
				}

				$httpProvider.defaults.headers.get["If-Modified-Since"] = "Mon, 1 Jan 1997 05:00:00 GMT";
				$httpProvider.defaults.headers.get["Cache-Control"] = "no-cache";
				$httpProvider.defaults.headers.get["Pragma"] = "no-cache";

				// Routes : route defined at /modules/core/routes
				router.route("routeAuth");

				// We do not initially want an otherwise route check, that's handled in app.js,
				// so we rely on run to check if the Auth header we already have (if any) is valid first
				// and if it is, we go into the app.  If not, we go to /login
			} catch (e) {
				console["error"]("An error occurred in configuration of application boot.  The error was:", e);
			}
		}
	])
	.run([
		"$window",
		"$location",
		"$http",
		"$rootScope",
		"$state",
		"$timeout",
		"$stateParams",
		"$localStorage",
		"$uibModal",
		"$uuid",
		"$sessionStorage",
		"$uibModalStack",
		"skI18nService",
		"$q",
		"rbacService",
		"$transitions",
		function($window, $location, $http, $rootScope, $state, $timeout,
			$stateParams, $localStorage, $uibModal,
			$uuid, $sessionStorage, $uibModalStack, skI18nService, $q, rbacService, $transitions) {
			const $ = angular.element;


			// First thing we need to do is init I18N, everything else will follow from
			// that.
			$localStorage.initialBoot = true;
			$localStorage.timedout = false;

			var GATEWAY_URL = window["GATEWAY_URL"];
			var CONTEXT = window["contextPath"];
			var GO_TO_ERROR = window["GO_TO_ERROR_PAGE"];

			if (!GATEWAY_URL) {
				var port = $location.port();
				var host = $location.host();
				var protocol = $location.protocol().toLowerCase();
				GATEWAY_URL = protocol + "://" + host;
				var intPort = parseInt(port, 10);
				if (protocol === "http" && intPort !== 80) {
					GATEWAY_URL += ":" + port;
				}
				if (protocol === "https" && intPort !== 443) {
					GATEWAY_URL += ":" + port;
				}
				console["log"]("Configuring gateway URL as: [" + GATEWAY_URL + "].  Also setting UIROOTURL to the same value.");
				window["GATEWAY_URL"] = GATEWAY_URL;
				window["UIROOTURL"] = GATEWAY_URL;
			}

			$localStorage.API_GATEWAY_HOSTNAME = GATEWAY_URL;
			$localStorage.timedout = null;

			var registerAndBootPublicI18N = function(){
				var def = $q.defer();
				skI18nService.registerMessageBundle("msx-ui-platform-public", CONTEXT + "/webjars/i18n/public/i18n.json", "root");
				skI18nService.initI18N()["finally"](function(){
					def.resolve(true);
					$rootScope.__i18nServiceInitialized = true;
				});
				return def.promise;
			};

			var hasCheckedAndLoadedBrand = false;

			var stateChangeCheck = function(){
				if(hasCheckedAndLoadedBrand){
					$timeout(function(){
						$(".sk-auth-hidden").removeClass("sk-auth-hidden");
					}, 10);
				}
			};		
			
			$transitions.onSuccess({}, stateChangeCheck);
			$transitions.onError({}, stateChangeCheck);

			var loadCurrentBrand = function() {
				var def = $q.defer();
				$http({
						method: 'GET',
						url: GATEWAY_URL + "/administration/api/v1/themes/published/active"
					})
					.then(function(pubData) {
						if (pubData && pubData.data && pubData.data.responseObject) {
							var themes = pubData.data.responseObject;
							if (themes.length === 1) {
								try {
									var parsed = JSON.parse(themes[0].customCSS) || "";
									if(parsed && typeof parsed.css === "string"){
										parsed.css = parsed.css.replace(/\.\.\/images\//gi, "../webjars/images/");
									}
									var location = $("head");
									$("<style></style>", {
										"id": "appCustom"
									}).appendTo(location);
									$("#appCustom")[0].innerHTML = parsed.css;
									$timeout(function() {
										$(window).trigger('resize');
									}, 100);
									if (parsed.strings) { // check for custom localization strings
										$localStorage.themeCustomResourceStrings = parsed.strings;
									} else {
										$localStorage.themeCustomResourceStrings = null;
									}
									parsed = "";
									pubData = "";
								} catch (ex) {
									$localStorage.themeCustomResourceStrings = null;
									console["error"]("There was an issue parsing the theme data.  Not applying", ex);
								}
							} else {
								$localStorage.themeCustomResourceStrings = null;
								console["error"]("There is more than one active theme, ignoring.");
							}
						}
						def.resolve(true);
					}, function(brandError) {
						// No brand/404, so just resolve.
						$localStorage.themeCustomResourceStrings = null;
						if (brandError && brandError.status !== 404) {
							// No custom theme, which is fine.
							console["log"]("A non-404 error occurred looking up custom brand.  Server response was: ", brandError);
						}
						def.resolve(true);
					});
				return def.promise;
			};

			// First thing is to check for any stale access tokens, and if there is a stale one, clear it.  Then
			// we init I18n, check saml config, and load the current brand.  Only then do we go to tthe rest of the flow.
			loadCurrentBrand().then(function(){
				registerAndBootPublicI18N()["finally"](function(){
					hasCheckedAndLoadedBrand = true;
					// We have loaded the brand and any needed i18n, now we can show stuff.
					$(".sk-auth-hidden").removeClass("sk-auth-hidden");
					// Adding this so that swapping between screens doesn't cause a blip-flicker.
					$timeout(function(){
						$("body").addClass("sk-fully-initialized");
					}, 500);
					if (!$sessionStorage.tabId) {
						$sessionStorage.tabId = $uuid();
					}
					var url;
					$localStorage.resourceStrings = $localStorage.resourceStrings || {};
					$localStorage.resourceStrings.currentValue = $localStorage.resourceStrings.currentValue || 0;

					var paths = ["/changepassword", "/forgotpassword", "/otpverify"];
					var isPublicPath = false;
					var k;
					for (k = 0; k < paths.length; k++) {
						if ($location.url().indexOf(paths[k]) === 0) {
							isPublicPath = true;
						}
					}

					try {
						// We aren"t authorized, so lets check the url data
						// to see if we need to redirect, or if we"re okay for
						// the moment in terms of a target location.
						url = $location.url();

						if (url) {
							if (url != "/login" &&
								url != "/error" &&
								url != "/loading" &&
								url != "/otpverify" &&
								url.indexOf("/login?") < 0 &&
								url.indexOf("/changepassword") < 0 &&
								url.indexOf("/forgotpassword") < 0) {
								$state.go("auth.login");
							}
						} else {
							if(GO_TO_ERROR){
								$state.go("auth.error");
							}else{
								$state.go("auth.login");
							}
						}
					} catch (ex) {
						// If any sort of error occurs trying to check the url, just drop to login.
							if(GO_TO_ERROR){
								$state.go("auth.error");
							}else{
								$state.go("auth.login");
							}
					}
					$rootScope.$state = $state;
					$rootScope.$stateParams = $stateParams;
				});
			});
		}
	]);

