import module from "../../../core/systemServices/module_platformBoot";
import TEMPLATE from "./ResetPassword-template.html";

module.component('msxResetPassword', {
	bindings: {}, // Create new isolate scope.
	template: TEMPLATE,
	controller: ['$element', '$localStorage', '$scope', '$http', '$rootScope', '$state', '$stateParams', 'skKeyConstants', 
	'$timeout', "$uuid", "$q", "msx.translateService", "msx.a11yMixins",
	function($element, $localStorage, $scope, $http, $rootScope, $state, $stateParams, keys, $timeout, $uuid, $q, translateService, a11yMixins) {
		const self = this;
		$element.addClass("auth-page reset-password-page");
		const angular = window["angular"];
		const $ = angular.element;
		let backTimeout = null;
		a11yMixins.mixinTo(self);

		self.username = $stateParams.username || "";
		self.userId = $stateParams.userId || "";
		self.resetToken = $stateParams.token;
		self.type = $stateParams.type;
		self.email = $stateParams.userId;
		self.showCharctersCheck = false;
		self.invalidPasswordMatch = false;
		self.invalidPassword = false;
		self.randomPasswordFieldSuffix = $uuid();
		
		self.password = "";
		// These are the original default rules used in the password
		// suggestion dialog.
		self.rules = [
			"public.cisco.resetpassword.characteristics.rule1",
			"public.cisco.resetpassword.characteristics.rule2",
			"public.cisco.resetpassword.characteristics.rule3",
			"public.cisco.resetpassword.characteristics.rule4",
			"public.cisco.resetpassword.characteristics.rule5"
		];

		self.returnToLogin = function(params) {
			if (backTimeout) {
				$timeout.cancel(backTimeout);
			}
			backTimeout = null;

			if ($localStorage.SAML_CONFIG &&
				$localStorage.SAML_CONFIG.loginMethod &&
				$localStorage.SAML_CONFIG.loginMethod.toLowerCase() === "saml") {

				$state.go("auth.samlRedirect", {
					obj: {
						SAML_CONFIG: $localStorage.SAML_CONFIG,
						targetUrl: window["LOGIN_URL"]
					}
				});
			} else {
				$state.go("auth.login", params);
			}
		};

		let url = "";
		let GATEWAY_URL = window["GATEWAY_URL"];
		
		if (self.type === 'EU') {
			url = GATEWAY_URL + '/idm/api/v1/users/identity';
		} else if (self.type === 'RU') {
			url = GATEWAY_URL + '/manage/api/v1/serviceinstances/remoteusers/identity';
		} else if (self.type === 'ASDMADMIN') {
			url = GATEWAY_URL + '/cgps/api/v1/serviceinstances/cgpsadmin/identity';
		} else {
			self.linkExpired = true;
			return;
		}
		
		// Function to get a suggested password from the server.
		let getPasswordSuggestion = function(){				
			if(self.__pwDef){
				return self.__pwDef;
			}
			
			const def = $q.defer();
			self.__pwDef = def;
			
			$http({
				method: "GET",
				url: GATEWAY_URL + "/idm/api/v1/users/suggestPassword/" + self.userId + "/token/" + self.resetToken
			}).then(function(resp){
				let pw = resp && resp.data ? resp.data.responseObject || "ERROR" : "ERROR";
				if(pw === "ERROR"){
					self.errorMessage = "public.cisco.reset.password.reload.suggested.failure.label";
				}
				def.resolve(pw);
				self.__pwDef = null;
			},function(error){
				if(error && error.status){
					if(error.status == 400){
						// Token expired.
						self.errorMessage = "public.cisco.reset.password.token.expired.failure.label";
					}else if (error.status == 404){
						// User not found or not associated to the user.
						self.errorMessage = "public.cisco.reset.password.token.mismatch.failure.label";
					}else if (error.status == 406){
						// Problem on the back end.
						self.errorMessage = "public.cisco.reset.password.reload.suggested.failure.label";
					}else{
						self.errorMessage = "public.cisco.reset.password.reload.suggested.failure.label";
					}
					def.resolve("");
					self.__pwDef = null;
				}else{
					def.resolve("ERROR");
					self.errorMessage = "public.cisco.reset.password.reload.suggested.failure.label";
					self.__pwDef = null;
				}
			});
			
			return def.promise;
		};						
		
		// Load the rules from the backend ...
		const rulesUrl = GATEWAY_URL + "/idm/api/v1/pwdpolicy/userId/" + self.userId + "/token/" + self.resetToken;
		$http({
				method: 'GET',
				url: rulesUrl
			})
			.then(function(resp) {
				let rules = resp && resp.data && resp.data.responseObject ? resp.data.responseObject.validRules || [] : [];
				self.rules = rules;
			}, function(resp) {
				console["error"]("There was an issue getting the rules from the back end.  Default guidelines will be displayed.   Error was: ", resp);
			});

		self.resetPasswordText = translateService("public.cisco.resetpassword.text", {
			username: $stateParams.userId
		});

		$http({
				method: 'PUT',
				url: url,
				data: {
					'action': 'SHOW_ACCEPT_NEW_PASSWORD',
					'token': self.resetToken,
					'payload': {
						'userId': self.email
					}
				}
			})
			.then(function(resp) {
				self.userId = resp.data && resp.data.responseObject ? resp.data.responseObject.userId || "" : "";
			}, function(resp) {
				let status = resp.status;
				if (status != 404) {
					self.linkExpired = true;
					self.errorMessage = translateService("public.cisco.resetpassword.linkexpired.error");
				}

			});
			
		self.showRecomendation = function() {
			if (self.displayRecomendation) {
				self.displayRecomendation = false;
			} else {
				self.displayRecomendation = true;
				$timeout(function() {
					$(".remote-user-info-close").focus();
				}, 100);
			}
		};

		self.closeInfo = function() {
			self.displayRecomendation = false;
			$timeout(function() {
				$(".show-recommendations-icon").focus();
			}, 100);
		};

		self.loginRedirect = function() {
			$state.go('auth.login');
		};

		self.clearPassword = function() {
			self.password = null;
			self.confirmPassword = null;
		};

		// This function implements basic A11Y redirect.  It will translate an enter or a space
		// on a tab-able element into a click.
		self.customA11yHandler = function(evt) {
			if (evt && evt.target) {
				let tag = evt.target.tagName ? evt.target.tagName.toLowerCase() : "";
				if (tag == 'input' && evt.keyCode == keys.ENTER) {
					self.resetPassword(self.password, self.confirmPassword);
				}
				self.a11yHandler(evt);
			}
		};

		self.toggleShowPassword = function(evt) {
			if (evt && evt.target) {
				// Handle the space key, redirect it to a click.
				if (evt.charCode === keys.SPACE || evt.keyCode === keys.ENTER) {
					evt.preventDefault();
					evt.stopPropagation();
					$timeout(function() {
						self.showCharctersCheck = !self.showCharctersCheck;
					}, 0);
				}
			}
		};

		self.resetPassword = function(pass, confPass) {
			self.successMessage = "";
			self.errorMessage = "";

			if (!pass || !confPass) {
				self.errorMessage = "public.cisco.resetpassword.label.confirmpassword.missing.error";
				self.confirmPassword = null;
				$("#password", $element[0]).focus();
				return;
			}

			if (pass !== confPass) {
				self.invalidPasswordMatch = true;
				self.errorMessage = "public.cisco.resetpassword.label.confirmpassword.error";
				self.confirmPassword = null;
				$("#password", $element[0]).focus();
				return;
			}
			$http({
					method: 'PUT',
					url: url,
					data: {
						"action": "RESET_PASSWORD",
						"token": self.resetToken,
						"payload": {
							"userId": self.userId,
							"password": pass,
							"confirmPassword": confPass
						}
					}
				})
				.then(function() {
					self.successMessage = "public.cisco.resetpassword.success";
					backTimeout = $timeout(function() {
						self.returnToLogin();
					}, 5000);
				}, function(resp) {
					const data = resp.data;
					let msg = data.message;
					msg = msg.replace(/, /gi, "<br>&nbsp;&nbsp;&nbsp;");
					msg = msg.replace(/\[/gi, "<br>&nbsp;&nbsp;&nbsp;");
					msg = msg.replace(/\]/gi, "");
					self.errorMessage = msg;
					self.confirmPassword = null;
					$("#password", $element[0]).focus();
				});
		};
		
		self.focusHandler = function(fieldId){
			const field = $("#" + fieldId);
			if(field && field.length){
				const v = $(field).val();
				if(!v){
					// This may seem weird, but it serves a purpose.  It prevents
					// the browser from popping its native password suggest dialog from
					// its password manager.  The browsers don't prompt for a readonly field
					// on focus.  So, setting readonly, and clearing it a little later
					// prevents it.
					$(field).attr("readonly", true);
					$timeout(function(){
					$(field).removeAttr("readonly");
					}, 10);
				}
				if(fieldId === "password" && !v){
					self.showPasswordSuggest = true;
				}
			}
		};
		self.blurHandler = function(fieldId){
			if(fieldId === "password"){
				$timeout(function(){
					const ae = document.activeElement;
					if(ae){
						if(!$(ae).closest(".auto-suggest-password-box").length){
							self.showPasswordSuggest = false;
						}
					}else{
						self.showPasswordSuggest = false;
					}
				}, 10);
			}else{
				self.showPasswordSuggest = false;
			}
		};
		self.passwordInput = function(){
			$timeout(function(){
				let pw = self.password || $("#password").val();
				console.log(self, self.password);
				if(pw && pw !== ""){
					self.showPasswordSuggest = false;
				}
			}, 100);
		};					
		self.handleSuggestionReload = function(){
			$timeout(function(){
				self.suggestedPassword = null;
				getPasswordSuggestion().then(function(pw){
					self.suggestedPassword = pw;
				});
				$("#password").focus();
			}, 0);
		};
		
		self.selectSuggestion = function(){
			if(self.suggestedPassword){
				self.password = self.suggestedPassword;
				self.confirmPassword = self.suggestedPassword;
			}
			$("#password").focus();
		};
		
		$scope.$watch("$ctrl.password", function(newVal){
			if(newVal){
				self.showPasswordSuggest = false;
			}
		});
		$scope.$watch('$ctrl.password', function() {
			self.invalidPasswordMatch = false;
			self.invalidPassword = false;
		});
		$scope.$watch('$ctrl.confirmPassword', function() {
			self.invalidPasswordMatch = false;
			self.invalidPassword = false;
		});
		
		
		self.handleSuggestionReload();
		
		$scope.$onDestroy = function() {
			if (backTimeout) {
				$timeout.cancel(backTimeout);
				backTimeout = null;
			}
		};
		
		self.$postLink = function(){
			$(".sk-auth-hidden").removeClass("sk-auth-hidden");
		};
		
		// Bind to any update events we need to be aware of.
		$scope.$on("updated-active-language", function() {
			if (self.linkExpired) {
				self.errorMessage = translateService("public.cisco.resetpassword.linkexpired.error");
			}
			// Update the password text too on any language change
			self.resetPasswordText = translateService("public.cisco.resetpassword.text", {
				username: $stateParams.userId
			});
		});		
	}]
});

