import module from "../../../module_platformBoot";
import TEMPLATE from "./AuthFailure-template.html";

module.component("msxAuthFailure", {
	bindings: {},
	template: TEMPLATE,
	controller: ["$rootScope", "$localStorage", "$timeout", "$sce", "$scope", "$state", "$location",
		"$window", "$element", "skKeyConstants", "msx.translateService",
		function($rootScope, $localStorage, $timeout, $sce, $scope, $state, $location, $window, $element, keys, translateService) {
			const self = this;
			const GATEWAY_URL = window["GATEWAY_URL"];
			const UIROOTURL = window["UIROOTURL"];
			self.csrfToken = window["csrfToken"] || "";						
			self.contextPath = window["contextPath"] || "";
			self.errorMessage = window["authErrors"] || "";
			$element.addClass("auth-page login-page sk-font-standard-text");
			
			const angular = window["angular"];
			const $ = angular.element;

			self.busy = true; // Busy Indicator on by default
			
			const checkI18n = function(){
				// Check to see if i18n is loaded, it affects if a main loader is shown or not.
				if ($rootScope.__i18nServiceInitialized || self.i18nLoaded) {
					self.busy = false;
					self.i18nLoaded = true;
				}else{
					$timeout(function(){
						checkI18n();
					}, 333);
				}
			};
			checkI18n();
			$scope.$on("updated-active-language", function() {
				self.busy = false;
				self.i18nLoaded = true;
			});

			self.version = window["MSX_VERSION"] || "v4.0.0";

			// compose parameters from query url
			$localStorage.API_GATEWAY_HOSTNAME = GATEWAY_URL;

			self.returnToLogin = function(event) {
				$timeout(function() {
					window.location=UIROOTURL;
				}, 100);
				if(event && event.preventDefault){
					event.preventDefault();
				}
				if(event && event.stopPropagation){
					event.stopPropagation();
				}
			};

			self.checkForEnter = function(evt) {
				if (evt && evt.target) {
					// Handle the space key, redirect it to a click.
					if (evt.keyCode === keys.ENTER) {
						evt.preventDefault();
						evt.stopPropagation();
						$("#__otp-view-return-button").focus();
						$timeout(function() {
							$("#__otp-view-return-button").trigger("click");
						}, 0);
					}
				}
			};
			
			self.$postLink = function() {
				checkI18n();
				const setPageTitle = function(){
					var pageTitle = translateService("public.cisco.login.app.title");
					if (pageTitle !== "public.cisco.login.app.title") {
						document.title = pageTitle;
					} else {
						pageTitle = translateService("cisco.application.title");
						if(pageTitle !== "cisco.application.title"){
							document.title = pageTitle;
						}
					}
				};
				setPageTitle();
				$scope.$on("updated-active-language", function() {
					setPageTitle();
				});
			};
		}
	]
});
