
import module from "./module_systemServices";

module.factory('rbacService', ['_', '$http', '$localStorage', '$q', function(_, $http, $localStorage, $q) {
	function retrieve() {
		var deferred = $q.defer();
		$localStorage.capabilities = [];
		// Changed IDM_URL to GATEWAY_URL
		$http.get($localStorage.API_GATEWAY_HOSTNAME + '/idm/api/v1/users/capabilities')
			.then(function(resp) {
				deferred.resolve(resp.data);
			}, function(resp) {
				deferred.reject({
					message: "Failed to extract authorizations (user capabilities)."
				});
			});
		return deferred.promise;
	}

	function ifAllow(capability) {
		capability = capability || "";
		if(!$localStorage || !$localStorage.capabilities){
			console["warn"]("RBAC:  Local storage of capabilities is not initialized, defaulting to false");
			return false;
		}
		var capabilityArray = capability.split(' ');
		// We need to check for ! capabilities, to allow for specific it is NOT an operator for example
		// So split the contents into teo parts, the positives, and the negatives.
		var newCapabilities = [];
		var notCapabilities = [];
		_.each(capabilityArray, function(val) {
			if(val && val.charAt(0) === "!"){
				notCapabilities.push(val.substring(1, val.length));
			}else{
				newCapabilities.push(val);
			}
		});
		capabilityArray = newCapabilities;
		// If there are no capabilities to check, or if the capabilities desired were in the user capabilities, and NONE of the NOT capabilties (if any) were present in
		// the user capabilities, then this action is allowed.
		return (!capabilityArray.length || $localStorage.capabilities.length > _.difference($localStorage.capabilities, capabilityArray).length) && 
			(!notCapabilities.length || $localStorage.capabilities.length === _.difference($localStorage.capabilities, notCapabilities).length);
	}
	
	// Function to check the permissions needed to determine if
	// this person is authorized.
	var isAuthorized = function(allowTokens) {
		var hasAuth = true;
		if (allowTokens) {
			if (typeof allowTokens === "string") {
				allowTokens = [allowTokens];
			}
			_.forEach(allowTokens, function(tokenSet) {
				hasAuth = hasAuth && ifAllow(tokenSet);
			});
		}
		return hasAuth;
	};
	
	return {
		retriveCapablities: retrieve,
		ifAllow: ifAllow,
		isAuthorized: isAuthorized
	};
	
}]);