import module from "../../../module_platformBoot";
import TEMPLATE from "./Otp-template.html";

module.component("msxOtp", {
	bindings: {},
	template: TEMPLATE,
	controller: ["$rootScope", "$timeout", "$localStorage", "$sce", "$scope", "$state", "$location",
		"$window", "_", "$element", "skKeyConstants", "msx.translateService",
		function($rootScope, $timeout, $localStorage, $sce, $scope, $state, $location, $window, _, $element, keys, translateService) {
			const self = this;
			const angular = window["angular"];
			const $ = angular.element;
			const GATEWAY_URL = window["GATEWAY_URL"];
			
			$element.addClass("auth-page login-page otp-page sk-font-standard-text");

			self.csrfToken = window["csrfToken"] || "";						
			self.contextPath = window["contextPath"] || "";
			self.errorMessage = window["authErrors"] || "";
			self.optResendEndpoint = window["optResendEndpoint"] || "";
			self.optVerifyEndpoint = window["optVerifyEndpoint"] || "";
			self.csrfParameterName = window["csrfParameterName"] || "";
			self.otpParam = window["otpParam"] || "";
			
			self.resendAction = self.contextPath + self.optResendEndpoint;
			self.verifyAction = self.contextPath + self.optVerifyEndpoint;
			
			self.busy = true; // Busy Indicator on by default

			$scope.$on("updated-active-language", function() {
				self.busy = false;
				self.i18nLoaded = true;
			});

			self.version = window["MSX_VERSION"] || "v4.0.0";

			$localStorage.API_GATEWAY_HOSTNAME = GATEWAY_URL;
			$localStorage.twoFactorAuthenticationComplete = false;

			self.resendotp = function() {
				self.loading = true;
				const form = $(".form-opt-resend");
				form.attr("action", self.resendAction);
				self.loading = true;
				$timeout(function() {
					form.submit();
				}, 100);
			};

			self.otpLogin = function(password, evt) {
				self.errorMessage = "";
				self.password = password || self.password;
				if(evt){
					if(evt.preventDefault){
						evt.preventDefault();
					}
					if(evt.stopPropagation){
						evt.stopPropagation();
					}
				}								
				const form = $(".form-otp-verify");
				form.attr("action", self.verifyAction);
				self.loading = true;
				$timeout(function() {
					form.submit();
				}, 100);
			};

			self.checkForEnter = function(evt) {
				if (evt && evt.target) {
					// Handle the space key, redirect it to a click.
					if (evt.keyCode === keys.ENTER) {
						evt.preventDefault();
						evt.stopPropagation();
						$("#__otp-view-submit-button").focus();
						$timeout(function() {
							$("#__otp-view-submit-button").trigger("click");
						}, 0);
					}
				}
			};
			
			self.$postLink = function(){
				const setPageTitle = function(){
					var pageTitle = translateService("public.cisco.login.app.title");
					if (pageTitle !== "public.cisco.login.app.title") {
						document.title = pageTitle;
					} else {
						pageTitle = translateService("cisco.application.title");
						if(pageTitle !== "cisco.application.title"){
							document.title = pageTitle;
						}
					}
				};
				setPageTitle();
				$scope.$on("updated-active-language", function() {
					setPageTitle();
				});
				// Check to see if i18n is loaded, it affects if a main loader is shown or not.
				$timeout(function(){
					if ($rootScope.__i18nServiceInitialized) {
						self.busy = false;
						self.i18nLoaded = true;
					}
				}, 100);
			};
		}
	]
});
