import module from "./index";

module.service("msx.a11yMixins", ["$timeout", "skKeyConstants", function($timeout, keys){
	const obj = {};
	const angular = window["angular"];
	const $ = angular.element;
	obj.a11yHandler = function(evt) {
		if (evt && evt.target) {
			var tag = evt.target.tagName ? evt.target.tagName.toLowerCase() : "";
			var processIt = true;

			if (tag === "textarea" || tag === "input" || tag === "button") {
				processIt = false;
			}

			if (processIt) {
				if (evt.charCode === keys.SPACE || evt.keyCode === keys.ENTER) {
					evt.preventDefault();
					evt.stopPropagation();
					$timeout(function() {
						$(evt.target).trigger("click");
					}, 0);
				}
			}
		}
	};
	
	obj.mixinTo = function(tObj){
		const newObj = {};
		$.extend(newObj, obj);
		delete newObj.mixinTo;
		$.extend(tObj, newObj); 
	};
	
	return obj;
	
}]);