const angular = window["angular"];
const moduleName = 'skPlatformBoot';
let module;
try {
	module = angular.module(moduleName);
} catch (err) {
	// named module does not exist, so create one
	module = angular.module(moduleName, ["ng",
		"skPlatformBootPrereqs",
		'ui.router',
		"ui.bootstrap",
		"ngStorage",
		"ngSanitize",
		"ngUUID",
		"i18nHelpers",
		"msx.components.formfields",
		"focus-if",
		"msx.systemServices",
		"msx.platform.constants",
		"msx.core.a11yHelpers"
	]);
}
export default module;