/*
 * @name /msx.translateService.js
 * @fileOverview Contains msx.translateService.js info.
 */
import module from "./module_systemServices";

var substitute = function(msg, map) {
	msg = msg || "";
	map = map || {};
	return msg.replace(/\$\{([^\s\:\}]+)(?:\:([^\s\:\}]+))?\}/g, function(match, key) {
		return map[key] === undefined ? "${" + key + "}" : map[key];
	});
};

module.service('msx.translateService', [
	'$rootScope', '$localStorage', 'skSafeTextSvc', '_',
	function ($rootScope, $localStorage, skSafeTextSvc, _) {
		/**
		 * Utility wrapper for $rootScope.resolveResourceString. Primarily
		 * intended to abstract the implementation of messages being stored on the
		 * angular app's root scope as well as providing a shorthand. Accessed as
		 * '$t' from the angular injection container.
		 *
		 * @param {string} str The message key to resolve
		 * @return {string} resolved resource string
		 */
		 
		if (!$rootScope.resolveResourceString) {
			// Define this only once on rootScope, it may already be available
			$rootScope.resolveResourceString = function(element, map) {
				if(element == null){
					element = "";
				}
				if(typeof element !== "string"){
					element = "" + element;
				}
				if(element == ""){
					return "";
				}
				var element_array = element.split(" ");
				var key_array = [];
				var msg = ($rootScope && $rootScope.msg) || $localStorage.msg || {};
				_.each(element_array, function(key) {
					key_array.push(msg[key] || key);
				});
				// SafeText removes bad HTML, script injects, etc, so we can safely inject the text/html.
				var res = skSafeTextSvc.render(key_array.join(" "));
				if (map && typeof map === "object") {
					res = substitute(res, map);
				}
				// Check for branded-theme custom strings
				if($localStorage.themeCustomResourceStrings && $localStorage.themeCustomResourceStrings[$localStorage.locale] && $localStorage.themeCustomResourceStrings[$localStorage.locale][element]) {
					res = $localStorage.themeCustomResourceStrings[$localStorage.locale][element];
				}
				// Check for theme preview custom strings
				if($localStorage.themePreviewResourceStrings && $localStorage.themePreviewResourceStrings[$localStorage.locale] && $localStorage.themePreviewResourceStrings[$localStorage.locale][element]) {
					res = $localStorage.themePreviewResourceStrings[$localStorage.locale][element];
				}
				return res;
			};
		}			 
		return function () {
			return $rootScope.resolveResourceString.apply($rootScope, arguments);
		};
	}
]);

module.filter('msxTranslate', [
	'msx.translateService',
	function(translateService) {
		var l10nHelperFilter = function(input) {
			return translateService(input || '');
		};
		return l10nHelperFilter;
	}
]);