const angular = window["angular"];
const moduleName = 'skPlatformBootPrereqs';
let module;
try {
	module = angular.module(moduleName);
} catch (err) {
	// named module does not exist, so create one
	module = angular.module(moduleName, ['ng']);
}
export default module;