/**
 *   Component to simplify/unify dialog displays
 *       <msx-modal-dialog
 *          template="/views/someview/templates/someTemplate.html"
 *          controller-name="someController"
 *          control-event="some.dialog.event.name"
 *          dialog-name="device"
 *          dialog-resolutions="somePropertyResolverObject">
 *       </msx-modal-dialog>
 */
import TEMPLATE from "./Dialog-template.html";

import module from "../index";

module.component('msxModalDialog', {
	bindings: {
		template: "@",
		templateString: "<?",
		component: "@",
		show: "=?",
		controllerName: "@",
		dialogResolutions: "=?",
		dialogWindowName: "@?",
		controlEvent: '@'
	},
	template: TEMPLATE,
	controller: ['$scope', '$uibModal', '$rootScope', function($scope, $uibModal, $rootScope) {
		const self = this;
		const angular = window["angular"];
		const $ = angular.element;

		// This function handles showing the modal if not already shown.
		var showModal = function(options) {
			if (!self._mInst) {
				let inst;
				if(self.component){
					inst = $uibModal.open({
						animation: false,
						component: self.component,
						resolve: options.resolutions || self.dialogResolutions,
						windowClass: self.dialogWindowName ? self.dialogWindowName + "-modalBox" : "",
						backdrop: 'static'
					});
				}else if(self.templateString){
					inst = $uibModal.open({
						animation: false,
						template: self.templateString,
						controller: self.controllerName,
						resolve: options.resolutions || self.dialogResolutions,
						windowClass: self.dialogWindowName ? self.dialogWindowName + "-modalBox" : "",
						backdrop: 'static'
					});
				}else{
					inst = $uibModal.open({
						animation: false,
						templateUrl: self.template,
						controller: self.controllerName,
						resolve: options.resolutions || self.dialogResolutions,
						windowClass: self.dialogWindowName ? self.dialogWindowName + "-modalBox" : "",
						backdrop: 'static'
					});
				}
				$rootScope.$broadcast("msx.dialog.opened", {});
				inst.closed.then(function() {
					self._mInst = null;
				});
				if(inst.rendered && inst.rendered.then){
					inst.rendered.then(function(){
						const wrapper = $("[uib-modal-window='modal-window']");
						const dialog = $(".modal-dialog", wrapper);
						if(dialog && dialog.length === 1){
							const focusableElements = $("[tabIndex='0']", dialog).filter(":visible");
							if(focusableElements && focusableElements.length){
								$(focusableElements[0]).focus();
							}else{
								$(dialog).focus();
							}
						}
					});
				}				
				self._mInst = inst;
			}
			return self._mInst;
		};

		// This function closes the modal if not closed.
		var closeModal = function() {
			if (self._mInst) {
				self._mInst.close();
			}
		};

		self.$postLink = function() {
			// This allows listening for user defined events to control the show or hide
			// of a dialog, allowing it to be controlled specifically by controllers within the dialog
			// contents.
			if (self.controlEvent) {
				$scope.$on(self.controlEvent, function(evt, data) {
					if (data && data.show != null) {
						if (data.show === true) {
							showModal({
								resolutions: data.resolutions
							});
						} else {
							closeModal();
						}
					}
				});
			}
		};

		self.$onChanges = function(changes) {
			const v = changes["show"];
			if (v) {
				if (v.currentValue === true) {
					showModal({});
				} else {
					closeModal();
				}
			}
		};

		// If this gets destroyed on a route or the like, make
		// sure the dialog is closed.
		self.$onDestroy = function() {
			closeModal();
		};
	}]
});