import module from "./module.js";
import currencies from "./json/currency.json";
import localeToCurrency from "./json/localeToCurrency.json"; 

module.service('currencyList', ["$rootScope", "_", "$localStorage", "localeList",
	function($rootScope, _, $localStorage, localeList) {
		const obj = {};
		const getLanguageKey = function(currency){
			currency = currency || "";
			currency = currency.toUpperCase();
			return "cisco.i18n.currency." + currency;
		};
		const getCurrency = function(currency){
			currency = currency || "";
			currency = currency.toUpperCase();
			return JSON.parse(JSON.stringify(currencies[currency] || null));
		};
		const getCurrencies = function(){
			return JSON.parse(JSON.stringify(currencies));
		};
		const getDefaultCurrencyForLocale = function(locale){
			let currencyCode = "USD";
			if(locale){
				locale = localeList.normalizeLocale(locale);
				if(locale.length > 2){
					const split = locale.split("_");
					const specificLocale = split[1] || split[0];
					currencyCode = localeToCurrency[specificLocale] || currencyCode;
				}else{
					currencyCode = localeToCurrency[locale.toUpperCase()] || currencyCode;
				}
			}
			return currencyCode;
		};
		obj.getLanguageKey = getLanguageKey;
		obj.getCurrency = getCurrency;
		obj.getCurrencies = getCurrencies;
		obj.getDefaultCurrencyForLocale = getDefaultCurrencyForLocale;
		return obj;
	}]);
