/*
 * @name /msx.loggerService.js
 * @fileOverview Contains msx.loggerService.js info.
 */
import module from "./module_platformBoot";
module.service('msx.loggerService', [
	function() {
		const console = window["console"] || function(){};
		var obj = {
			log: function(){
				console["log"].apply(window, arguments);	
			},
			error: function(){
				console["error"].apply(window, arguments);	
			},
			warn: function(){
				console["warn"].apply(window, arguments);	
			}
		};
		return obj;
	}
]);