import module from "./module.js";

module.service('localeList', [function() {
	const obj = {};

	const normalizeLocale = function(locale){
		let bLocale = locale;
		if (bLocale) {
			bLocale = bLocale.toLowerCase();
			var blocArr = bLocale.split("_");
			if (blocArr.length === 2) {
				bLocale = blocArr[0] + "_" + blocArr[1].toUpperCase();
			}
			blocArr = bLocale.split("-");
			if (blocArr.length === 2) {
				bLocale = blocArr[0] + "_" + blocArr[1].toUpperCase();
			}
			if (bLocale.length == 2) {
				// Map some languages, the short name to the
				// standardized base name.
				switch (bLocale) {
					case "de":
						bLocale = "de_DE";
						break;
					case "fr":
						bLocale = "fr_FR";
						break;
					case "it":
						bLocale = "it_IT";
						break;
					case "ja":
						bLocale = "ja_JP";
						break;
					default:
						bLocale = bLocale || "en_US";
						break;
				}
			}
		}
		return bLocale;
	};

	const getLanguageKey = function(locale){
		locale = normalizeLocale(locale);
		return "public.cisco.i18n.language." + locale;
	};
	obj.getLanguageKey = getLanguageKey;
	obj.normalizeLocale = normalizeLocale;

	return obj;
}]);