/*
 * @name /skBrowserDetectionService.js
 * @fileOverview Contains skBrowserDetectionService
 * This module is a browser detection utility.  It should only be used for things like
 * checking browser support (as in app tested and verifying if supported list].  It should
 * not generally be used to branch code, but just generally to inform the user what browser
 * they seem to be on if needed to do so in an application
 */
import module from "./module_systemServices";
var $ = angular.element;
var bd = {
	isWebkit: undefined,
	isTrident: undefined,
	isEdge: undefined,
	isIE: undefined,
	isGecko: undefined,
	isFirefox: undefined,
	isChrome: undefined,
	isOpera: undefined,
	isSafari: undefined
};

var n;
try {
	n = navigator;
} catch (ignore) {}

if (n) {
	var ua = n.userAgent;
	var av = n.appVersion;
	var tv = parseFloat(av);
	var uaKeys = ["Gecko", "Firefox", "Konqueror", "WebKit", "Trident", "Edge", "Minefield", "Chrome", "OPR", "MSIE", "Safari"];
	var i, key;
	var matchMap = {};
	var verMap = {};
	for (i = 0; i < uaKeys.length; i++) {
		key = uaKeys[i];
		matchMap[key] = ua.indexOf(key) >= 0;
	}
	var browsers = {
		"Firefox": matchMap["Firefox"] && !matchMap["Konqueror"] &&
			!matchMap["WebKit"] && !matchMap["Trident"] &&
			!matchMap["Edge"] && matchMap["Firefox"] &&
			(parseFloat(ua.split("Firefox/")[1] || ua.split("Minefield/")[1]) || undefined),
		"Gecko": matchMap["Gecko"] && !matchMap["Konqueror"] && !matchMap["WebKit"] &&
			!matchMap["Trident"] && !matchMap["Edge"] &&
			(parseFloat(ua.split("Gecko/")[1]) || undefined),
		"Webkit": matchMap["WebKit"] && !matchMap["Trident"] &&
			!matchMap["Edge"] &&
			(parseFloat(ua.split("WebKit/")[1]) || undefined),
		"Chrome": matchMap["Chrome"] && matchMap["WebKit"] && !matchMap["Trident"] &&
			!matchMap["Edge"] && !matchMap["OPR"] &&
			(parseFloat(ua.split("Chrome/")[1]) || undefined),
		"Edge": matchMap["WebKit"] & matchMap["Chrome"] && matchMap["Edge"] &&
			(Math.floor(parseFloat(ua.split("Edge/")[1])) || undefined),
		"Safari": matchMap["WebKit"] && matchMap["Safari"] && !matchMap["Trident"] &&
			!matchMap["Chrome"] && !matchMap["Edge"] &&
			(parseFloat(ua.split("Version/")[1]) || undefined),
		"IE": matchMap["MSIE"] && !matchMap["Gecko"] && !matchMap["Edge"] &&
			(parseFloat(ua.split("MSIE ")[1]) || undefined),
		"Opera": matchMap["WebKit"] && matchMap["OPR"] && matchMap["Chrome"] &&
			(parseFloat(ua.split("OPR/")[1]) || undefined),
		"Trident": matchMap["Trident"] && matchMap["Gecko"] && !matchMap["Edge"] &&
			(parseFloat(ua.split("Trident/")[1]) || undefined)
	};
	for (key in browsers) {
		var browser = key.toLowerCase();
		browser = browser.charAt(0).toUpperCase() + browser.substring(1, browser.length);
		if (browser === "Ie") {
			browser = "IE";
		}
		bd["is" + browser] = browsers[key] || undefined;
	}
}

var isBrowser = function(browserName) {
	browserName = browserName ? browserName.toLowerCase() : "";
	var isB = false;
	switch (browserName) {
		case "ff":
		case "firefox":
			if (bd.isFirefox) {
				isB = true;
			}
			break;
		case "gecko":
			if (bd.isGecko) {
				isB = true;
			}
			break;
		case "safari":
			if (bd.isSafari) {
				isB = true;
			}
			break;
		case "chrome":
			if (bd.isChrome) {
				isB = true;
			}
			break;
		case "webkit":
			if (bd.isWebkit) {
				isB = true;
			}
			break;
		case "trident":
			if (bd.isTrident) {
				isB = true;
			}
			break;
		case "edge":
			if (bd.isEdge) {
				isB = true;
			}
			break;
		case "ie":
		case "internet explorer":
			if (bd.isIE) {
				isB = true;
			}
			break;
		case "opera":
			if (bd.isOpera) {
				isB = true;
			}
			break;
		default:
			isB = false;
	}
	return isB;
};

// Attach a high contrast mode CSS class to HTML, in case
// we need to support specific styles for High Contrast mode later.
$(document).ready(function() {
	try {
		if (document.documentElement) {
			// Lets go ahead and add in some browser specific selectors on <html> just in
			// case we need to special case a browser some day.  Ideally we will not need
			// to, but this helps out.
			var classes = "";
			if (isBrowser("chrome")) {
				classes += " vms-bd-chrome";
			}
			if (isBrowser("webkit")) {
				classes += " vms-bd-webkit";
			}
			if (isBrowser("safari")) {
				classes += " vms-bd-safari";
			}
			if (isBrowser("opera")) {
				classes += " vms-bd-opera";
			}
			if (isBrowser("ff")) {
				classes += " vms-bd-ff";
			}
			if (isBrowser("ie")) {
				classes += " vms-bd-ie";
			}
			if (isBrowser("trident")) {
				classes += " vms-bd-trident";
			}
			if (isBrowser("edge")) {
				classes += " vms-bd-edge";
			}
			if (classes) {
				$(document.documentElement).addClass(classes);
			}
		}
	} catch (ignore) {}
});

module.service('skBrowserDetectionService', [function($q, $localStorage, $http) {
	this.isBrowser = isBrowser;
	// This function returns the version for the requested browser, if the current environment matches
	// the noted browser.  For example, Webkit and Chrome will both be true in the Chrome browser.
	// So, you can ask for which one, if you want.  If none are specified, it just returns the first
	// one that denotes a version
	this.getBrowserVersion = function(browser) {
		if (browser) {
			browser = browser.toLowerCase();
			switch (browser) {
				case "ff":
				case "firefox":
					return bd.isFirefox;
				case "gecko":
					return bd.isGecko;
				case "safari":
					return bd.isSafari;
				case "chrome":
					return bd.isChrome;
				case "webkit":
					return bd.isWebkit;
				case "trident":
					return bd.isTrident;
				case "edge":
					return bd.isEdge;
				case "ie":
				case "internet explorer":
					return bd.isIE;
				case "opera":
					return bd.isOpera;
				default:
					return null;
			}
		} else {
			// No browser specified
			let key, ver;
			for (key in bd) {
				ver = bd[key];
				if (ver != null) {
					return ver;
				}
			}
		}
	};
	this.browserFlags = bd;
}]);