import module from "./module";

module.service("msx.i18nMixins", ["msx.translateService", function(translateService){
	const obj = {};
	const angular = window["angular"];
	const $ = angular.element;
	obj.getTranslation = function(str, props) {
		return translateService(str || "", props);
	};
	
	obj.mixinTo = function(tObj){
		const newObj = {};
		$.extend(newObj, obj);
		delete newObj.mixinTo;
		$.extend(tObj, newObj); 
	};
	return obj;
}]);