/**
 * @fileOverview /modules/core/routes/route.auth.js
 * @description Defines states for auth pages.
 */
(function(angular) {
	'use strict';

	angular.module('skPlatformBoot')
		.provider('routeAuth', ['$stateProvider', '$controllerProvider', '$compileProvider', '$provide',
			function($stateProvider, $controllerProvider, $compileProvider, $provide) {
				var providers = {
					$controllerProvider: $controllerProvider,
					$compileProvider: $compileProvider,
					$provide: $provide
				};
				this.setup = function() {
					$stateProvider
						.state('auth', {
							views: {
								'content': {
									component: "msxAuth"
								}
							},
							resolve: {
								"_i18nMods": ["$q", "skI18nService", "$localStorage", function($q, skI18nService, $localStorage){
									$localStorage.locale = null;
									var registerAndBootPublicI18N = function(){
										var def = $q.defer();
										skI18nService.registerMessageBundle("msx-ui-platform-public", "/idm/webjars/i18n/public/i18n.json", "root");
										skI18nService.initI18N()["finally"](function(){
											def.resolve(true);
										});
										return def.promise;
									};
									return registerAndBootPublicI18N();
								}]
							},
							"abstract": true
						})
						.state('auth.login', {
							url: '/login',
							component: "msxLogin",
							"public": true,
							params: {
								username: null,
								password: null
							}
						})
						.state('auth.otp', {
							url: '/otpverify',
							component: "msxOtp",
							"public": true
						})
						.state('auth.error', {
							url: '/error',
							component: "msxAuthFailure",
							"public": true
						})
						.state('auth.appLoading', {
							url: '/loading',
							component: "msxAppLoading",
							"public": true
						})
						.state('auth.resetPassword', {
							url: '/changepassword?token&type&userId',
							component: "msxResetPassword",
							"public": true
						})
						.state('auth.forgotPassword', {
							url: '/forgotpassword',
							component: "msxForgotPassword",
							params: {
								username: null
							},
							"public": true
						});
				};

				var getJSONFromParam = function (queryString) {
					var _ = window["_"];
					var queryStrArry = _.last(window.location.hash.split('?')).split('&');
					var result = {};
					queryStrArry.forEach(function (pair) {
						pair = pair.split('=');
						result[pair[0]] = decodeURIComponent(pair[1] || '');
					});
					return result;
				};
				this.$get =angular.noop;
			}
		]);
})(window.angular);
