import module from "../../../module_platformBoot";
import TEMPLATE from "./Login-template.html";

/**
@name _testStorage_localstorage
@function
@private
@desc Function to see if local storage is supported.
*/
const _testStorage_localstorage = function() {
	let ret = false;
	try {
		const ls = window["localStorage"];
		if (ls) {
			ret = true;
		}
	} catch (ignore) {}
	return ret;
};

module.component("msxLogin", {
	bindings: {},
	template: TEMPLATE,
	controller: ["$rootScope", "$sce", "$scope", "$http", "$state", "$location",
		"$window", "$element", "skKeyConstants", "$localStorage", "$timeout", "$stateParams", "skI18nService",
		"msx.translateService", "msx.i18nMixins",
		function($rootScope, $sce, $scope, $http, $state, $location, $window, $element, keys, $localStorage,
			$timeout, $stateParams, skI18nService, translateService, i18nMixins) {
			const self = this;
			const angular = window["angular"];
			const $ = angular.element;
			i18nMixins.mixinTo(self);

			const GATEWAY_URL = window["GATEWAY_URL"];

			$element.addClass("auth-page login-page sk-font-standard-text");

			const SAML_LOGOUT_CONFIG = $localStorage.SAML_LOGOUT_CONFIG;

			if(SAML_LOGOUT_CONFIG){
				// Corner case, someone tried to go to the login page
				// by changing the URL.  Kick them back to the SAML
				// login.
				const slc = $localStorage.SAML_LOGOUT_CONFIG;
				$localStorage.SAML_LOGOUT_CONFIG = null;
				$timeout(function(){
					$state.go("auth.samlRedirect", {
						obj: {
							SAML_CONFIG: slc.SAML_CONFIG,
							targetUrl: window["LOGIN_URL"]
						}
					});
				}, 0);
				return;
			}

			self.csrfToken = window["csrfToken"] || "";
			self.contextPath = window["contextPath"] || "";
			self.errorMessage = window["authErrors"] || "";
			self.endPoint = window["authEndpoint"] || "";
			self.csrfParameterName = window["csrfParameterName"] || "";

			self.loginAction = self.contextPath + self.endPoint;

			// Remove this state.
			self.__rememberMeStateRestore = true;
			self.busy = true; // Busy Indicator on by default

			//Check to see if Validate OTP page has to be displayed by checking if first factor is done or not.
			if (!$localStorage.twoFactorAuthenticationComplete) {
				self.twoFactorAuthenticationRequired = false;
			}

			if (self.twoFactorAuthenticationRequired && !$localStorage.twoFactorAuthenticationComplete) {
				$state.go("auth.login");
			}

			$scope.$on("updated-active-language", function() {
				self.busy = false;
				self.i18nLoaded = true;
			});

			self.version = window["MSX_VERSION"] || "v4.0.0";

			$localStorage.API_GATEWAY_HOSTNAME = GATEWAY_URL;
			$localStorage.twoFactorAuthenticationComplete = false;

			self.login = function(username, password, evt) {
				if(self.loading){
					return;
				}
				self.errorMessage = "";
				username = username || self.username;
				password = password || self.password;
				if(evt){
					if(evt.preventDefault){
						evt.preventDefault();
					}
					if(evt.stopPropagation){
						evt.stopPropagation();
					}
				}
				// If we"re logging in, clear the auth token before calling.
				$http.defaults.headers.common.Authorization = "";
				self.loading = true;
				delete $localStorage.Authorization;
				$localStorage.timedout = false;
				let form = $(".form-signin");
				$(form).attr("action", self.loginAction);
				if(form && form.length){
					$timeout(function(){
						form[0].submit();
					}, 0);
				}
			};

			self.forgotPassword = function() {
				$state.go("auth.forgotPassword", {
					username: self.username
				});
			};

			/**
				@name _saveRememberMe
				@function
				@private
				@desc Function to store the value of the rememberMe flag.  If it is cleared, then the username is also cleared.
			*/
			let _saveUsername = function() {
				if (self.__rememberMeStateRestore) {
					return;
				}
				let u = self.username || "";
				let cName = "vmsLoginScreen_" + "_username";
				cName = cName.replace(/ /gi, "_");
				if (_testStorage_localstorage()) {
					let ls = window["localStorage"];
					if (ls && ls.setItem) {
						ls.setItem(cName, u);
					}
				}
			};

			/**
			@name _restoreUsername
			@function
			@private
			@desc Function to restore the username from local storage, if any.
			*/
			self._restoreUsername = function() {
				try {
					let cName = "vmsLoginScreen_" + "_username";
					cName = cName.replace(/ /gi, "_");
					let c;
					let empty = false;
					$("#username", $($element)).each(function() {
						let hasAutoFillNode;
						try {
							hasAutoFillNode = $("input:-webkit-autofill", $($element));
						} catch (ignore) {
							hasAutoFillNode = [];
						}
						if ($(this).val() === "" && !hasAutoFillNode.length) {
							empty = true;
						}
					});
					if (empty) {
						if (_testStorage_localstorage()) {
							let ls = window["localStorage"];
							if (ls && ls.getItem) {
								c = ls.getItem(cName);
							}
						}
						if (c) {
							self.username = c;
							let pElem = $("#password", $($element))[0];
							if (pElem && pElem.focus) {
								pElem.focus();
							}
						}
					}
				} catch (e) {
					console["warn"](this.id + ": An error occurred while trying to restore the user name from localstorage.", e);
				}
			};

			/**
			@name _saveRememberMe
			@function
			@private
			@desc Function to store the value of the rememberMe flag.  If it is cleared, then the username is also cleared.
			*/
			self._saveRememberMe = function() {
				if (self.__rememberMeStateRestore) {
					return;
				}
				let rm = self.rememberMe;
				let cName = "vmsLoginScreen_" + "_rememberme";
				let cName2 = "vmsLoginScreen_" + "_username";
				cName = cName.replace(/ /gi, "_");
				cName2 = cName2.replace(/ /gi, "_");
				if (_testStorage_localstorage()) {
					let ls = window["localStorage"];
					if (ls && ls.setItem) {
						ls.setItem(cName, rm + "");
						_saveUsername();
					}
					if (!rm) {
						// Clear the old user name from storage.
						// Timeout needed. no idea why.
						setTimeout(function() {
							ls.removeItem(cName2);
						}, 0);
					}
				}
			};

			/**
			@name _restoreRememberMe
			@function
			@private
			@desc Function to restore the value of the rememberMe flag
			*/
			self._restoreRememberMe = function() {
				try {
					let cName = "vmsLoginScreen_" + "_rememberme";
					cName = cName.replace(/ /gi, "_");
					let c;
					if (_testStorage_localstorage()) {
						let ls = window["localStorage"];
						if (ls && ls.getItem) {
							c = ls.getItem(cName);
						}
					}
					if (c && c === "true") {
						self.rememberMe = true;
						_saveUsername();
					}
				} catch (e) {
					console["warn"]("An error occurred while trying to restore the remember me state from the browser local storage.", e);
				}
			};

			$scope.$watch("$ctrl.rememberMe", function() {
				$timeout(function() {
					self._saveRememberMe();
				}, 100);
			});

			$scope.$watch("$ctrl.username", function() {
				$timeout(function() {
					self._saveRememberMe();
				}, 100);
			});

			self.hasLocalStorage = function() {
				return _testStorage_localstorage();
			};

			self.toggleRememberMeValue = function(evt) {
				if (evt && evt.target) {
					// Handle the space key, redirect it to a click.
					if (evt.charCode === keys.SPACE || evt.keyCode === keys.ENTER) {
						evt.preventDefault();
						evt.stopPropagation();
						$timeout(function() {
							self.rememberMe = !self.rememberMe;
						}, 0);
					}
				}
			};

			self.checkForEnter = function(evt) {
				if (evt && evt.target) {
					// Handle the space key, redirect it to a click.
					if (evt.keyCode === keys.ENTER) {
						evt.preventDefault();
						evt.stopPropagation();
						$("#__login-view-submit-button").focus();
						$timeout(function() {
							$("#__login-view-submit-button").trigger("click");
						}, 0);
					}
				}
			};

			if ((!$localStorage.initialBoot || $localStorage.timedout) && ($localStorage.Authorization || $http.defaults.headers.common.Authorization)) {
				// If we"re not in the initial boot cycle, we need to check to see if the
				// auth token we already have is valid or not, and if not,
				// clear it.

				$localStorage.timedout = null;
				if ($rootScope.sessionPoller) {
					clearInterval($rootScope.sessionPoller);
					delete $rootScope.sessionPoller;
				}
				const i18nLoadHandler = function(){
					$rootScope.__i18nServiceInitialized = true;
					self.busy = false;
					self.i18nLoaded = true;
				};
				$http({
						method: "GET",
						url: $localStorage.API_GATEWAY_HOSTNAME + "/idm/api/v1/isTokenValid"
					})
					.then(function(resp) {
						const status = resp.status;
						if (status != 200) {
							// No valid token, throw to login.
							$http.defaults.headers.common.Authorization = "";
							$localStorage.Authorization = null;
							if ($stateParams.username && $stateParams.password) {
								self.username = $stateParams.username;
								self.password = $stateParams.password;
								$timeout(function() {
									self.login();
								}, 100);
							}
						}
						if (!$localStorage.msg || !$rootScope.msg) {
							skI18nService.initI18N()["finally"](i18nLoadHandler);
						}
					}, function() {
						// Definitely wasn"t a valid request, so kill the tokens in
						// local storage and the header so that new login is required.
						$http.defaults.headers.common.Authorization = "";
						delete $localStorage.Authorization;
						if (!$localStorage.msg || !$rootScope.msg) {
							skI18nService.initI18N()["finally"](i18nLoadHandler);
						}
					});
			} else {
				delete $localStorage.initialBoot;
				if (!$localStorage.msg || !$rootScope.msg) {
					skI18nService.initI18N();
				}
				if ($stateParams.username && $stateParams.password) {
					self.username = $stateParams.username;
					self.password = $stateParams.password;
					$timeout(function() {
						self.login();
					}, 100);
				}
			}
			
			self.$postLink = function(){
				$timeout(function() {
					self._restoreRememberMe();
					if (self.rememberMe) {
						self._restoreUsername();
					} else {
						$timeout(function() {
							$("#username").focus();
						}, 0);
					}
					$timeout(function() {
						delete self.__rememberMeStateRestore;
					}, 500);
				}, 50);
	
				const setPageTitle = function(){
					let pageTitle = translateService("public.cisco.login.app.title");
					if (pageTitle !== "public.cisco.login.app.title") {
						document.title = pageTitle;
					} else {
						pageTitle = translateService("cisco.application.title");
						if(pageTitle !== "cisco.application.title"){
							document.title = pageTitle;
						}
					}
				};
				setPageTitle();
				$scope.$on("updated-active-language", function() {
					setPageTitle();
				});
				// Check to see if i18n is loaded, it affects if a main loader is shown or not.
				$timeout(function(){
					if ($rootScope.__i18nServiceInitialized) {
						self.busy = false;
						self.i18nLoaded = true;
					}
				}, 100);
			};
		}
	]
});