import module from "./module_platformBoot";
module.service('skSafeTextSvc', ['DOMPurify', 'he', function(DOMPurify, he) {
	this.render = function(string) {
		let res = DOMPurify.sanitize(he.unescape(string));
		if(res == null){
			res = "";
		}
		if(typeof res === "object"){
			res = res.toString() || "";
		}
		return res;
	};
}]);
