import module from "./module_bootPrereqs";
module.run(['$rootScope', 'skSafeTextSvc', '_', function($rootScope, skSafeTextSvc, _) {
	const angular = window["angular"];
	const $ = angular.element;
	$(window).on('keydown mousedown contextmenu', function(event) {
		if ((event.ctrlKey || event.metaKey) && event.shiftKey) { // Checking for Ctrl (or CMD) + shift.  Then we need to check the event type.
			if(event.type == 'contextmenu'){
				var emitWarn = false;
				if(event.target.attributes && (!event.target.attributes['sk-resource-string'] || !event.target.attributes['sk-resource-string'].value)) {
					if(!event.target.attributes['sk-resource-string-placeholder'] || !event.target.attributes['sk-resource-string-placeholder'].value) {
						emitWarn = true;
					}
				}
				if(emitWarn){
					console["warn"]("ctrl (or cmd) + shift + contextmenu event, ignoring it and blocking default behavior as it is unlikely this really was a context menu call.  Target: ", event.target );
				}
				event.stopPropagation();
				event.preventDefault();
			}else if(event.type == 'mousedown'){
				// Okay, we're in a command/ctrl + shift + click.  Lets see about swapping the view for any sk-resource-string enabled elements.
				if (event.target.attributes && event.target.attributes['sk-resource-string'] && event.target.attributes['sk-resource-string']) {
					event.target.innerText = skSafeTextSvc.render($rootScope.change(event.target.attributes['sk-resource-string'].value, event.target));
					if (event.target.attributes['sk-resource-string'].value == event.target.innerText) {
						event.target.style.color = "#49BA8E";
					} else {
						event.target.style.color = "";
					}
					event.stopPropagation();
					event.preventDefault();
				} else if (event.target.attributes && event.target.attributes['sk-resource-string-placeholder'] && event.target.attributes['sk-resource-string-placeholder'].value) {
					event.target.placeholder = $rootScope.change(event.target.attributes['sk-resource-string-placeholder'].value, event.target);
					event.stopPropagation();
					event.preventDefault();
				} else {
					console["warn"]("Inline ctrl + shift + click is not yet implemented on this element: ", event.target);
					var key_array = [];
					_.each($rootScope.msg, function(value, key) {
						if (event.target.innerText.trim() == (value && value.trim())) {
							key_array.push(key);
						}
					});
					if (key_array.length > 0) {
						var logThis = "The key you are looking for could be " + (key_array.length == 1 ? ": %c" + key_array[0] : "one among these: %c" + key_array.join(", "));
						console["log"](logThis, "color:green;");
					}
				}
			}
		}
	});
	// end of ctrl + shift + click listener for resource strings
}]);

