/**
 * @fileOverview /modules/core/routes/router.js
 * @description RouteProvider , responsible for setting up routes / states.
 */
(function(angular) {
	'use strict';
	angular.module('skPlatformBoot')
		.provider('router', ['$stateProvider', '$injector', function($stateProvider, $injector) {
			this.route = function(routeName) {
				var route = $injector.get(routeName + 'Provider');
				route.setup();
			};
			this.$get = angular.noop;
		}]);
})(window.angular);