import module from "../../core/eventListeners/module_bootPrereqs";
 
module.directive('skResourceString', ['$rootScope', '$localStorage', "_", "skSafeTextSvc", function($rootScope, $localStorage, _, skSafeTextSvc) {
	const angular = window["angular"];
	const $ = angular.element;
	const substituteMsgString = function(msg, map) {
		msg = msg || "";
		map = map || {};
		return msg.replace(/\$\{([^\s\:\}]+)(?:\:([^\s\:\}]+))?\}/g, function(match, key) {
			return map[key] === undefined ? "${" + key + "}" : map[key];
		});
	};

	$rootScope.$watch('msg', function() {
		// Resolve Resource String now uses safe HTML cleaning so html can now be used here.
		$rootScope.$broadcast("msx-root-message-obj-changed", {});
	});
	
	// Define this only once on rootScope, not each time the directive is used.
	$rootScope.resolveResourceString = function(element, map) {
		if(element == null){
			element = "";
		}
		if(typeof element !== "string"){
			element = "" + element;
		}
		if(element === ""){
			return "";
		}
		const element_array = element.split(" ");
		const key_array = [];
		const msg = ($rootScope && $rootScope.msg) || $localStorage.msg || {};
		
		_.each(element_array, function(key) {
			key_array.push(msg[key] || key);
		});
		// SafeText removes bad HTML, script injects, etc, so we can safely inject the text/html.
		let res = (skSafeTextSvc.render(key_array.join(" ")) || "").toString();
		if (map && typeof map === "object") {
			res = substituteMsgString(res, map);
		}
		// Check for branded-theme custom strings
		if($localStorage.themeCustomResourceStrings && $localStorage.themeCustomResourceStrings[$localStorage.locale] && $localStorage.themeCustomResourceStrings[$localStorage.locale][element]) {
			res = $localStorage.themeCustomResourceStrings[$localStorage.locale][element];
		}
		// Check for theme preview custom strings
		if($localStorage.themePreviewResourceStrings && $localStorage.themePreviewResourceStrings[$localStorage.locale] && $localStorage.themePreviewResourceStrings[$localStorage.locale][element]) {
			res = $localStorage.themePreviewResourceStrings[$localStorage.locale][element];
		}
		return res;
	};

	if(!$rootScope.change){
		// Used by the shift-click display of keys.
		$rootScope.change = function(key, targetElement, dataValue) {
			if (targetElement) {
				dataValue = $(targetElement).data('translationObj') || dataValue || null;
			}
			if(key && targetElement){
				var isResourceString = !!$(targetElement).data("isResourceString");
				$(targetElement).data("isResourceString", !isResourceString);
				if (isResourceString) {
					return key;
				}
			}
			var changed = $rootScope.resolveResourceString(key, dataValue);
			return changed;
		};
	}
			
	return {
		restrict: 'AE',
		scope: {
			skResourceString: "@",
			skResourceSubstitutions: "=?",
			skResourceStringSetTitle: "@"
		},
		template: "{{ resourceStringValue }}",
		controller: ['$scope', function($scope) {
			$scope.resourceStringValue = "binding";
			$scope._setTitle = false;
		}],
		link: function(scope, element) {
			let elem = element;
			scope.resourceStringValue = $rootScope.change(scope.skResourceString, elem, scope.skResourceSubstitutions);
			if(!elem.context){
				elem = $(elem[0]);
			}
			scope.$on("change-current-language", function(){
				// Resolve Resource String now uses safe HTML cleaning so html can now be used here.
				var subs = scope.skResourceSubstitutions;
				elem.html($rootScope.resolveResourceString(elem.attr('sk-resource-string'), subs || null));
			});
			
			scope.$on('msx-root-message-obj-changed', function() {
				// Resolve Resource String now uses safe HTML cleaning so html can now be used here.
				const subs = scope.skResourceSubstitutions || $(elem).data('translationObj') || null;
				elem.html($rootScope.resolveResourceString(elem.attr('sk-resource-string'), subs || null));
			});
	
			scope.$watchCollection('skResourceSubstitutions', function(newValue, oldValue, scope) {
				// Resolve Resource String now uses safe HTML cleaning so html can now be used here.
				if (newValue) {
					$(elem).data('translationObj', newValue);
				}
				var str = $rootScope.resolveResourceString(elem.attr('sk-resource-string'), newValue || null).toString();
				elem.html(str);
				if(scope._setTitle){
					elem.attr("title", str);
				}
				$(elem.contents()).css("color", "");
			}, false);
			scope.$watch('skResourceString', function(newValue, oldValue) {
				// Resolve Resource String now uses safe HTML cleaning so html can now be used here.
				if(newValue === oldValue){
					return;
				}
				const subs = scope.skResourceSubstitutions || $(elem).data('translationObj') || null;
				const str = $rootScope.resolveResourceString(elem.attr('sk-resource-string'), subs || null).toString();
				elem.html(str);
				$(elem.contents()).css("color", "");
				if(scope._setTitle && !scope._setTitleValue){
					elem.attr("title", str);
				}
			}, false);
			scope.$watch("skResourceStringSetTitle", function(newVal){
				if(newVal){
					const subs = scope.skResourceSubstitutions;
					if(newVal.toLowerCase() === "false"){
						scope._setTitle = false;
						scope._setTitleValue = false;
					}else if(newVal.toLowerCase() === "true"){
						scope._setTitle = true;
						elem.attr("title", $rootScope.resolveResourceString(elem.attr('sk-resource-string'), subs || null).toString());
					}else{
						scope._setTitle = false;
					}
				}else{
					scope._setTitle = false;
				}
			});
		}
	};
}]);
