import TEMPLATE from "./BusyIndicator-template.html";
// In this component, we need module to be Platform Boot level so don't use this shared module
//import module from "../index.js";
//module.component('msxBusyIndicator', {

const angular = window["angular"];
angular.module('skPlatformBoot').component('msxBusyIndicator', {
	bindings: {
		type: "@?",
		stroke: "@?",
		radius: "@?",
		speed: "@?",
		delay: "@?"
	},
	template: TEMPLATE,
	controller: ['$scope', '$element', '$timeout', function($scope, $element, $timeout) {
		var $ = angular.element;
		var self = this;
		
		var active = true; // Used to turn animation loop on/off
		
		self.$postLink = function(){
			// ***********************************
			var templateBuilder = function(htmltemplate, data) {
				// Jeff's nifty mini-templater. Loop through data object and replace any {{tags}} that have the same names as the object fields
				// ex: templateBuilder("<div id='{{ID}}'>{{STR}}</div>", { ID: 'myDiv', STR: 'Hello' }) returns "<div id='myDiv'>Hello</div>"
				for (var key in data) {
					htmltemplate = htmltemplate.replace(new RegExp('{{' + key + '}}', 'g'), data[key]);
				}
				return htmltemplate;
			};
	
			// ***********************************
			var easeInOutQuad = function(t) {
				return t < 0.5 ? 2 * t * t : -1 + (4 - 2 * t) * t;
			};
	
			var radiusConstants = {
				micro: 0.4,
				small: 0.8,
				medium: 1.2,
				large: 2.8,
				huge: 3.2,
				gigantic: 6.4
			};
			
			// Set defaults or accept overrides
			self._type = angular.isDefined(self.type) ? self.type : 'ease'; // option 'linear'
			self._stroke = angular.isDefined(self.stroke) ? parseFloat(self.stroke, 10) : 0.4; // in rems
			self._radius = angular.isDefined(self.radius) ? radiusConstants[self.radius] || parseFloat(self.radius, 10) : 3.2; // in rems
			self._delay = angular.isDefined(self.delay) ? parseInt(self.delay, 10) : 0;
			self._speed = angular.isDefined(self.speed) ? parseFloat(self.speed, 10) : 1;
	
			// SVG HTML template
			var html = '' +
				'<svg class="vms-busy" width="{{size}}" height="{{size}}" viewBox="0 0 {{size}} {{size}}">' +
				'   <circle class="vms-busy-meter" cx="{{center}}" cy="{{center}}" r="{{radius}}" stroke-width="{{stroke}}"></circle>' +
				'   <circle class="vms-busy-value" cx="{{center}}" cy="{{center}}" r="{{radius}}" stroke-width="{{stroke}}"></circle>' +
				'</svg>';
				
			var modifier = $("body").parent().css("font-size") || 10;
			if(typeof modifier == "string"){
				if(modifier.indexOf("px") > -1){
					modifier = parseInt(modifier, 10);
				}else{
					modifier = 10;
				}
			}
	
			// Replace all {{}} macros in the HTML string with values from object
			var absoluteValue = function(remVal){
				return remVal * modifier;
			};
	
			html = templateBuilder(html, {
				stroke: absoluteValue(self._stroke),
				radius: absoluteValue(self._radius),
				center: absoluteValue(self._radius) + absoluteValue(self._stroke),
				size: (absoluteValue(self._radius) + absoluteValue(self._stroke)) * 2
			});
	
			var busyValueElem;
			var startTimeStamp = 0;
	
			// Macro Values
			var CIRCUMFERENCE = (2 * Math.PI * absoluteValue(self._radius)); // *10 converts rems to pixels)
	
			// ********************************
			var raf = window.requestAnimationFrame;
			var parentCtx = this;
			var doAnim = function(t) {
				if (!startTimeStamp) {
					startTimeStamp = t;
				}
	
				// elapsed delta 0.0 - 1.0 advances the filled snake, >1.0 - 2.0 retreats it
				var delta = ((t - startTimeStamp) / 1000) * parentCtx._speed;
				// default animation lasts for 2 seconds (2000ms | delta 2.0) at which point we reset to 0
				if (delta > 2.0) {
					startTimeStamp = t;
					delta = 0;
				}
	
				var indicatorOffset = delta; // linear
				if (parentCtx._type === 'ease') {
					indicatorOffset = (delta <= 1.0) ? easeInOutQuad(delta) : easeInOutQuad(delta - 1.0) + 1.0;
				}
	
				// Position the animated snake
				busyValueElem.style.strokeDashoffset = CIRCUMFERENCE * (1 - (indicatorOffset));
	
				// Kick off the next animation frame unless we've been hidden or destroyed
				if (active) {
					raf(doAnim);
				}
			};
	
			// Delayed start of animation if specified
			$timeout(function() {
				// Make sure it hasn't been turned off during the timeout period
				if (active) {
					// Manually insert the SVG code into the Angular wrapper (extra step to avoid initial errors reported when Angular tries to do injection to SVG)
					$element.html(html);
					busyValueElem = $('.vms-busy-value', $element)[0];
					busyValueElem.style.strokeDasharray = CIRCUMFERENCE; // Set initial animation size parameter
					doAnim(0); // Kick off the animation
				}
	
			}, self._delay);
		};

		// Stop the animation loop if the element is removed
		self.$onDestroy = function() {
			active = false;
		};
	}]
});
