import module from "../../../core/systemServices/module_platformBoot";
import TEMPLATE from "./ForgotPassword-template.html";

module.component("msxForgotPassword", {
	bindings: {},
	template: TEMPLATE,
	controller: ["$http", "$state", "$stateParams", "$timeout", "$element",
		function($http, $state, $stateParams, $timeout, $element) {
		$element.addClass("auth-page forgot-password-page");
		const self = this;
		const GATEWAY_URL = window["GATEWAY_URL"];
		const url = GATEWAY_URL + "/idm/api/v1/users/identity";
		const angular = window["angular"];
		const $ = angular.element;
		let backTimeout = null;

		self.username = $stateParams.username || "";

		self.returnToLogin = function() {
			if (backTimeout) {
				$timeout.cancel(backTimeout);
			}
			backTimeout = null;
			$state.go("auth.login");
		};

		self.sendReset = function(username) {
			$http({
					method: "PUT",
					url: url,
					data: {
						"action": "SEND_PASSWORD_RESET_NOFICATION",
						"token": "",
						"payload": {
							"userId": username,
							"password": "",
							"confirmPassword": ""
						}
					}
				})
				.then(function() {
					self.successMessage = "public.cisco.forgotpassword.success";
					backTimeout = $timeout(function() {
						self.returnToLogin();
					}, 5000);
				}, function() {
					self.errorMessage = "public.cisco.forgotpassword.failure";
					backTimeout = $timeout(function() {
						self.returnToLogin();
					}, 5000);
				});
		};
		self.$postLink = function(){
			$(".sk-auth-hidden").removeClass("sk-auth-hidden");
		};		
	}]
});